// client/src/serviceWorkerRegistration.js

// Fonction pour enregistrer le service worker
export function register() {
    if ('serviceWorker' in navigator && process.env.NODE_ENV === 'production') {
      window.addEventListener('load', () => {
        const swUrl = '/service-worker.js';
  
        registerValidSW(swUrl);
        
        // Ajouter des gestionnaires pour suivre l'tat du rseau
        handleNetworkStatusChanges();
      });
    }
}

  
function registerValidSW(swUrl) {
    navigator.serviceWorker
      .register(swUrl)
      .then((registration) => {
        console.log('Service Worker enregistr avec succs');
        
        // Configurer les mises  jour automatiques
        registration.onupdatefound = () => {
          const installingWorker = registration.installing;
          if (installingWorker == null) {
            return;
          }
          
          installingWorker.onstatechange = () => {
            if (installingWorker.state === 'installed') {
              if (navigator.serviceWorker.controller) {
                //  ce stade, une nouvelle version du service worker a t installe
                console.log('Nouvelle version du Service Worker disponible');
                
                // Montrer une notification pour informer l'utilisateur
                showUpdateNotification();
              } else {
                // Premier Service Worker install, application prte pour le offline
                console.log('Application prte pour une utilisation hors ligne');
              }
            }
          };
        };
        
        // Activer les vnements de synchronisation en arrire-plan
        setupBackgroundSync(registration);
      })
      .catch((error) => {
        console.error('Erreur lors de l\'enregistrement du Service Worker:', error);
      });
}
  
// Dsinscrire le service worker
export function unregister() {
    if ('serviceWorker' in navigator) {
      navigator.serviceWorker.ready
        .then((registration) => {
          registration.unregister();
        })
        .catch((error) => {
          console.error('Erreur lors de la dsinscription du Service Worker:', error);
        });
    }
}
  
// Monitoring du statut rseau
function handleNetworkStatusChanges() {
    // Mettre  jour l'interface utilisateur lorsque le statut du rseau change
    window.addEventListener('online', () => {
      document.body.classList.remove('offline');
      document.body.classList.add('online');
      
      // Tenter de synchroniser les donnes en attente
      syncPendingData();
    });
    
    window.addEventListener('offline', () => {
      document.body.classList.remove('online');
      document.body.classList.add('offline');
      
      // Afficher une notification  l'utilisateur
      showOfflineNotification();
    });
    
    // Appliquer la classe initiale
    if (navigator.onLine) {
      document.body.classList.add('online');
    } else {
      document.body.classList.add('offline');
    }
}
  
// Synchronisation des donnes en attente lorsque la connexion est rtablie
function syncPendingData() {
    if ('serviceWorker' in navigator && 'SyncManager' in window) {
      navigator.serviceWorker.ready
        .then(registration => {
          // Tenter de synchroniser les photos
          registration.sync.register('sync-photos')
            .catch(err => console.error('Erreur lors de l\'enregistrement de la synchronisation des photos:', err));
          
          // Tenter de synchroniser les RSVP
          registration.sync.register('sync-rsvp')
            .catch(err => console.error('Erreur lors de l\'enregistrement de la synchronisation des RSVP:', err));
        });
    }
}
  
// Configurer la synchronisation en arrire-plan
function setupBackgroundSync(registration) {
    if ('SyncManager' in window) {
      // Dj configur dans le service worker
    } else {
      console.log('La synchronisation en arrire-plan n\'est pas supporte par ce navigateur');
    }
}
  
// Afficher une notification lors de la mise  jour du service worker
function showUpdateNotification() {
    const event = new CustomEvent('swUpdate', { detail: { updateAvailable: true } });
    window.dispatchEvent(event);
}
  
// Afficher une notification lorsque l'utilisateur passe hors ligne
function showOfflineNotification() {
    // Vous pouvez implmenter une notification UI ici si ncessaire
    console.log('Vous tes maintenant hors ligne. Certaines fonctionnalits peuvent tre limites.');
}